@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Element/Button";

$card-width: 513px;
@mixin cardContainer {
    min-width: 100%;
    @include screen.xl {
        min-width: $card-width;
        max-width: $card-width;
    }
}

.internalContainer {
    @include Margin.set($left: beta, $right: beta, $top: gamma);
    @include Flex.set($dir: column, $grow: alpha, $align: center, $justify: top);
    gap: Layout.get-space(delta);

    @include screen.md {
        @include Margin.set($left: delta, $right: delta, $top: delta);
    }

    @include screen.xl {
        grid-column: 2;
    }
    #{Text.$selector}.title {
        @include Margin.set($bottom: alpha);
        @include Font.set($size: delta);
        @include Color.set($fg: "black");

        @include screen.md {
            @include Font.set($size: kappa);
            @include Margin.set($bottom: beta);
        }
    }

    #{Text.$selector}.optionTitle {
        @include Font.set($size: gamma, $weight: default);
        word-break: break-word;
        @include Color.set($fg: black);
    }

    #{Text.$selector}.optionBold {
        @include Font.set($size: gamma, $weight: bold);
        word-break: break-word;
        @include Color.set($fg: black);
    }
    #{Text.$selector}.subtitle {
        @include Font.set($family: default, $weight: default);
    }
}

.list {
    min-width: 100%;
    @include screen.xl {
        min-width: $card-width;
    }
}

.cardAmount {
    @include cardContainer;
    form {
        @include screen.xl {
            gap: Layout.get-space(alpha);
        }
        @include Flex.set($dir: column, $align: center, $justify: between);
        gap: Layout.get-space(iota);
    }

    #{Text.$selector}.title {
        @include Margin.set($bottom: none);
        @include Font.set($align: left, $weight: bold, $size: delta);
        min-width: 100%;
    }

    .oversizedAmountContainer {
        @include Flex.set($dir: column, $align: center, $justify: center);
        gap: Layout.get-space(alpha);
        min-width: 100%;
        section {
            width: 100%;
        }
        .chipContainer {
            @include Flex.set($dir: row, $align: center, $justify: between);
            width: 100%;
        }
    }

    .submitButton {
        @include Padding.set($top: beta, $bottom: beta, $left: gamma, $right: gamma);
    }
}

.optionTerm {
    cursor: pointer;
    @include Flex.set($dir: row, $align: between, $justify: between);
    width: 100%;
}

.step4Title {
    @include Font.set($size: kappa, $weight: bold, $align: center);
    @include Color.set($fg: "black");
}

.step4Subtitle {
    @include Font.set($size: delta, $align: center);
    @include Color.set($fg: "black");
}
.step4SubtitleAmount {
    @include Font.set($size: delta, $align: center);
}
.staticAlertContainer {
    min-width: 100%;
    section {
        inline-size: 100%;
        overflow-wrap: break-word;
    }
    @include screen.xl {
        section {
            inline-size: $card-width;
            overflow-wrap: break-word;
            @include Padding.set($left: beta, $right: beta);
        }
    }
}
.step4Container {
    @include cardContainer;
    & form {
        & > div {
            @include Padding.set($preset: alpha);
            @include Flex.set($dir: column, $align: center, $justify: center);
            gap: Layout.get-space(gamma);
        }
    }

    .step4DetailsContainer {
        @include Flex.set($dir: column, $align: center, $justify: center);
        gap: Layout.get-space(beta);
        width: 100%;
        & div {
            @include Flex.set($dir: row, $align: center, $justify: between);
            gap: Layout.get-space(beta);
            width: 100%;
            #{AmountFormatter.$selector} > span,
            #{AmountFormatter.$selector} > data {
                @include Color.set($fg: black);
                @include Margin.set($preset: none);
                @include Font.set($align: center);
            }
            #{Text.$selector}:nth-child(2) {
                @include Color.set($fg: black);
            }
        }
    }

    .step4AmountLoan {
        #{AmountFormatter.$selector} > span,
        #{AmountFormatter.$selector} > data {
            @include Color.set($fg: black);
            @include Margin.set($preset: none);
            @include Font.set($size: kappa, $weight: bold, $align: center);
        }
    }

    .step4FinanceAmount {
        #{AmountFormatter.$selector} > span,
        #{AmountFormatter.$selector} > data {
            @include Color.set($fg: black);
            @include Margin.set($preset: none);
            @include Font.set($align: center);
        }

        section {
            @include Flex.set($dir: row, $align: center, $justify: center);
            @include Font.set($align: center);
        }
    }

    .buttonContainer {
        @include Flex.set($dir: column, $align: center, $justify: center);
        gap: Layout.get-space(alpha);
        #{Button.$selector} {
            @include Padding.set($top: beta, $bottom: beta, $left: gamma, $right: gamma);
        }
    }

    .termsAndConditions {
        width: 100%;
        @include Flex.set($justify: start);

        div {
            @include Flex.set($align: center);
            @include Margin.set($preset: none);
            #{Text.$selector} {
                @include Color.set($fg: "gray-dark");
                @include Font.set($transform: caps);
            }
            #{Button.$selector} {
                @include Button.asUnderlineLink;
                #{Text.$selector} {
                    @include Font.set($weight: default);
                }
            }
        }
    }
}

.step5Container {
    @include cardContainer;
    @include Flex.set($dir: column, $align: center, $justify: center);
    gap: Layout.get-space(gamma);

    .step5AmountLoan {
        #{AmountFormatter.$selector} > span,
        #{AmountFormatter.$selector} > data {
            @include Color.set($fg: black);
            @include Margin.set($preset: none);
            @include Font.set($size: kappa, $weight: bold, $align: center);
        }
    }

    .step5FinanceAmount {
        #{AmountFormatter.$selector} > span,
        #{AmountFormatter.$selector} > data {
            @include Color.set($fg: black);
            @include Margin.set($preset: none);
            @include Font.set($align: center);
        }

        section {
            @include Flex.set($dir: row, $align: center, $justify: center);
            @include Font.set($align: center);
        }
    }

    .step5DetailsContainer {
        @include Flex.set($dir: column, $align: center, $justify: center);
        gap: Layout.get-space(beta);
        width: 100%;
        & div {
            @include Flex.set($dir: row, $align: center, $justify: between);
            gap: Layout.get-space(beta);
            width: 100%;
            #{AmountFormatter.$selector} > span,
            #{AmountFormatter.$selector} > data {
                @include Color.set($fg: black);
                @include Margin.set($preset: none);
                @include Font.set($align: center);
            }
            #{Text.$selector}:nth-child(2) {
                @include Color.set($fg: black);
            }
        }
    }

    .step5TagContainer {
        @include Flex.set($dir: column, $align: center, $justify: center);
    }
}

.stepOTPContainer {
    @include cardContainer;
    form {
        @include Flex.set($dir: column, $align: center, $justify: start);
        @include Padding.set($preset: delta);
        gap: Layout.get-space(alpha);
        #{Text.$selector} {
            @include Font.set($align: center);
        }
    }
}
