// stylelint-disable at-rule-blacklist
@use "../size";

$name-xs: #{"xs"};
$name-sm: #{"sm"};
$name-md: #{"md"};
$name-lg: #{"lg"};
$name-xl: #{"xl"};

$touch-ios: "(-webkit-overflow-scrolling: touch)";
$sd-ios: "(-webkit-device-pixel-ratio: 2)";
$hd-ios: "(-webkit-device-pixel-ratio: 3)";

$xs: "(min-width: #{size.$width-xs})";
$sm: "(min-width: #{size.$width-sm})";
$md: "(min-width: #{size.$width-md})";
$lg: "(min-width: #{size.$width-lg})";
$xl: "(min-width: #{size.$width-xl})";

// iPhone 5 and below
$xs-ios: "(max-width: #{size.$width-xs-ios}) and (max-height: #{size.$height-xs-ios}) and #{$sd-ios}";
// iPhone 6, 6s, 7, 8
$sm-ios: "(max-width: #{size.$width-sm-ios}) and (max-height: #{size.$height-sm-ios}) and #{$sd-ios}";
// iPhone 10, 11Pro
$sm-ios-pro: "(max-width: #{size.$width-sm-ios}) and (max-height: #{size.$height-sm-ios-pro}) and #{$hd-ios}";
// iPhone 6+, 6s+, 7+, 8+
$sm-ios-plus: "(max-width: #{size.$width-sm-ios-plus}) and (max-height: #{size.$height-sm-ios-plus}) and #{$hd-ios}";
// iPhone 10r, 10s, 11, 11ProMax
$sm-ios-max: "(max-width: #{size.$width-sm-ios-plus}) and (max-height: #{size.$height-sm-ios-max})";
// iPhone 10r, 11
$sm-ios-max-sd: "#{$sm-ios-max} and #{$sd-ios}";
// iPhone 10s, 11ProMax
$sm-ios-max-hd: "#{$sm-ios-max} and #{$hd-ios}";

// -------------------------------------------------------------------------------------------------
//                                                                                 Feature detection
// -------------------------------------------------------------------------------------------------

@mixin -screen {
    @media only screen {
        @content;
    }
}

@mixin landscape {
    @include landscape {
        @content;
    }
}

@mixin portrait {
    @include portrait {
        @content;
    }
}

// Mobile Safari, and probably any other browser running in iOS.
@mixin touch-ios {
    @supports #{$touch-ios} {
        @content;
    }
}

@mixin sd-webkit {
    @media #{$sd-ios} {
        @content;
    }
}

@mixin hd-webkit {
    @media #{$hd-ios} {
        @content;
    }
}

// =================================================================================================
//                                                                                   e[X]tra [S]mall
//                                                          Old mobile devices and resized viewports
// =================================================================================================

@mixin xs {
    @include -screen {
        @media #{$xs} {
            @content;
        }
    }
}

// ------------------------------------------------------------------------------ iPhone 5 and below

@mixin xs-ios {
    @include touch-ios {
        @include -screen {
            @media #{$xs-ios} {
                @content;
            }
        }
    }
}

@mixin xs-ios-landscape {
    @include xs-ios {
        @include landscape {
            @content;
        }
    }
}

@mixin xs-ios-portrait {
    @include xs-ios {
        @include portrait {
            @content;
        }
    }
}

// =================================================================================================
//                                                                                           [SM]all
//                                                                 Most phones and resized viewports
// =================================================================================================

@mixin sm {
    @include -screen {
        @media #{$sm} {
            @content;
        }
    }
}

// ------------------------------------------------------------------------------------ SMall iphone
//                                                                                       6, 6s, 7, 8

@mixin sm-ios {
    @include touch-ios {
        @include -screen {
            @media #{$sm-ios} {
                @content;
            }
        }
    }
}

@mixin sm-ios-landscape {
    @include sm-ios {
        @include landscape {
            @content;
        }
    }
}

@mixin sm-ios-portrait {
    @include sm-ios {
        @include portrait {
            @content;
        }
    }
}

