// TODO: this was just left here for reference, DO NOT USE THIS YET.
@mixin sm-ios-all {
    @include sm-ios {
        @content;
    }

    @include iphone-lg-r {
        @content;
    }

    @include iphone-lg-s {
        @content;
    }
}

:export {
    NAME_XS: $name-xs;
    NAME_SM: $name-sm;
    NAME_MD: $name-md;
    NAME_LG: $name-lg;
}
