@use "." as this;
@use "@pkg/react-storybook/Component";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

$card-width: 513px;
$icon-min-width: 20px;

#{this.$selector} {
    @include Margin.set($left: auto, $right: auto);

    .wrapperOptionList {
        @include Border.set($radius: beta);
        @include Shadow.set($box: alpha);
        max-width: $card-width;

        & header {
            @include Flex.set($align: center, $justify: between);
            @include Margin.set($bottom: delta);

            & #{Text.$selector}.title {
                @include Font.set($size: delta, $weight: bold);
            }
            & #{Text.$selector}.description {
                @include Font.set($size: beta, $weight: default);
                @include Color.set($fg: "gray-light");
            }
        }

        & section.content {
            @include Margin.set($top: alpha, $bottom: alpha);

            @include screen.xl {
                @include Margin.set($top: delta, $bottom: delta);
            }

            .options {
                @include Flex.set($dir: column);
                gap: Layout.get-space(beta);
            }
            .option {
                @include Shadow.set($box: epsilon);
                cursor: pointer;
                width: 100%;

                .customOption {
                    @include Flex.set($justify: start, $dir: row, $align: center);
                    @include Border.set($style: default);
                    @include Color.set($bg: "white");
                    @include Padding.set($preset: none);
                    gap: calc(Layout.get-space(alpha) + Layout.get-letter-spacing(iota));
                    cursor: pointer;
                    width: Layout.get-width(kappa);
                }

                .optionRadio {
                    @include Margin.set($preset: none);
                    gap: Layout.get-space(alpha);
                    width: Layout.get-width(kappa);
                }

                svg:first-child {
                    min-width: $icon-min-width;
                }

                label {
                    text-transform: none;
                    white-space: normal;
                }
            }
        }

        & footer {
            @include Flex.set($align: center, $justify: center);

            #{Button.$selector} {
                @include Margin.set($top: beta, $bottom: alpha);
                @include Padding.set($left: beta, $right: beta);
                width: Layout.get-width(auto);
            }
        }
    }
}
